import React, { useState, useEffect } from 'react'
import { StyleSheet, View,Text, Image,TextInput, FlatList, Button,Dimensions,TouchableOpacity,ScrollView ,SafeAreaView} from 'react-native'
import moment from 'moment'

import db from '../../config/Firebase'
import * as firebase from 'firebase'
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
import UsersPosts from './UsersPosts';
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height

export default function SavedPost(props) {
    const [users,setUsers]=useState([]);
    const [posts,setPosts]=useState([]);
    const [user,setUser]=useState([]);
    useEffect(()=>{
        function fetchPost(post){
            db.collection('posts')
            .doc(post.user)
            .collection('userPosts')
            .doc(post.id)
            .get()
            .then((snapshot)=>{
                if(snapshot.exists){
                    post.posts=snapshot.data();
                    post.posts.id=snapshot.id;
                    setPosts(arr=>[...arr,post])
                }
                else{
                    alert("some thing is apense")
                }
            })
        }
        db.collection('savepost')
        .doc(firebase.auth().currentUser.uid)
        .collection("usersave")
        .orderBy("creation", "desc")
        .onSnapshot((snapshot)=>{
            snapshot.docs.map(doc=>{
                const post =doc.data();
                post.id=doc.id;
                fetchPost(post)
            })
        })
        return function cleanup() {
            setPosts([])
        }
    },[0])
    
    return (

        <View>
            {posts.length!==0?
                <FlatList
                numColumns={1}
                horizontal={false}
                data={posts}
                renderItem={({ item }) => (
                    <UsersPosts style={{paddingTop:5}} navigation={props.navigation.navigate} users={item.user} posts={item.posts}  uid={item.user} />
              )}

                />
            :
            <View>
                
            </View>
            }
        </View>
    )
}
