import React, { useState,useEffect } from 'react'
import { View, TextInput, Image, Button, Dimensions,TouchableOpacity ,Text} from 'react-native'

import * as firebase from 'firebase'
import { NavigationContainer } from '@react-navigation/native'
import db from '../../config/Firebase'
import { FontAwesome } from '@expo/vector-icons'
require("firebase/firestore")
require("firebase/firebase-storage")

const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height
function Save(props) {
    const [caption, setCaption] = useState("")
    const [users,setUsers]=useState([]);
    const [IMAGE,setIMAGE]=useState(props.route.params.image);
    const [Width,setWidth]=useState(props.route.params.w);
    const [Height,setHeight]=useState(props.route.params.h);
    useEffect(()=>{
        db.collection('users').doc(firebase.auth().currentUser.uid).get().then((snapshot)=>{setUsers(snapshot.data())})
    },[])
    const uploadImage = async () => {
        const uri = props.route.params.image;
        const childPath = `post/${firebase.auth().currentUser.uid}/${Math.random().toString(36)}`;
        console.log(childPath)

        const response = await fetch(uri);
        const blob = await response.blob();

        const task = firebase
            .storage()
            .ref()
            .child(childPath)
            .put(blob);

        const taskProgress = snapshot => {
            console.log(`transferred: ${snapshot.bytesTransferred}`)
        }

        const taskCompleted = () => {
            task.snapshot.ref.getDownloadURL().then((snapshot) => {
                savePostData(snapshot,props.route.params.w,props.route.params.w);
                console.log(snapshot)
            })
        }

        const taskError = snapshot => {
            console.log(snapshot)
        }

        task.on("state_changed", taskProgress, taskError, taskCompleted);
    }

    const savePostData = (downloadURL,width,height) => {

        firebase.firestore()
            .collection('posts')
            .doc(firebase.auth().currentUser.uid)
            .collection("userPosts")
            .add({
                downloadURL,
                caption,
                width,
                height,
                likesCount: 0,
                creation: firebase.firestore.FieldValue.serverTimestamp(),
                photo:downloadURL,
                uid:firebase.auth().currentUser.uid,
            }).then(function(){
                        props.navigation.popToTop();
            })
    }
    return (
        <View style={{ flex: 1 ,backgroundColor:'white', alignItems:'center', justifyContent: 'center',     alignItems: 'center', position:'absolute', zIndex:-1, width:screenWidth, height:screenHeight+50,}}>
            <View style={{flexDirection:'row',alignItems:'center',justifyContent:'space-between'}}>
                <TextInput  placeholder="Write a Caption . . ." onChangeText={(caption) => setCaption(caption)} style={{backgroundColor:'rgba(0,0,0,0.05)', fontSize:20, paddingVertical:10, paddingHorizontal:15, margin:5, width:'80%', borderRadius:10}}/>
                    <TouchableOpacity style={{margin:5}} onPress={()=>uploadImage()}>
                          <FontAwesome name='check' color={'blue'} size={35} style={{top:2}} />
                    </TouchableOpacity>
            </View>
            {console.log(props.route.params)}
            <View style={{flex:1, flexDirection:"row",margin:10}} >
                <Image source={{ uri: IMAGE }} style={{flex:1,width:screenWidth, height:400, }} />
            </View>
        </View>
    )
}

export default Save;