import React, { useState, useEffect } from 'react'
import { StyleSheet, View,TextInput, Text, Image, FlatList, Button,SafeAreaView,Dimensions,TouchableOpacity,ScrollView, ScrollViewBase } from 'react-native'
import *as firebase from 'firebase'
require('firebase/firestore')
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
import moment from 'moment'
import db from './../../config/Firebase';
import { useFonts } from 'expo-font';
import Chat from './Chat';
import UsersPosts from './UsersPosts';
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height

export default function Profiles(props) {
    const [user,setUser]=useState([]);
    const [users,setUsers]=useState([]);
    useEffect(()=>{
        db.collection('following')
        .doc(props.uid)
        .collection('userFollowing')
        .onSnapshot((snapshot)=>{
         let user=snapshot.docs.map(doc=>{
                    fetUsers(doc.id);
            })
            
        })
        
    },[0])
    function fetUsers(uid){
        db.collection('users')
        .doc(uid)
        .get()
        .then((snapshot)=>{
            let user=snapshot.data();
            user.uid=snapshot.id;
            FetchFolloWing(user);
        })
    }
    const onFollow = (userToFollow,item) => {
        db.collection("following")
             .doc(firebase.auth().currentUser.uid)
             .collection("userFollowing")
             .doc(userToFollow)
             .set({})
         db.collection('users').doc(firebase.auth().currentUser.uid).update({
                 following: firebase.firestore.FieldValue.arrayUnion(userToFollow)
             })
         db.collection('users').doc(userToFollow).update({
             followers: firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid)
             })
             item.follow=true;
     }
     const onUnfollow = (userToFollow,item) => {
        db.collection("following")
             .doc(firebase.auth().currentUser.uid)
             .collection("userFollowing")
             .doc(userToFollow)
             .delete()
             db.collection('users').doc(firebase.auth().currentUser.uid).update({
                 following: firebase.firestore.FieldValue.arrayRemove(userToFollow)
             })
             db.collection('users').doc(userToFollow).update({
                 followers: firebase.firestore.FieldValue.arrayRemove(firebase.auth().currentUser.uid)
             })
             item.follow=false;
     }
    function FetchFolloWing(u){
        db.collection('following')
        .doc(firebase.auth().currentUser.uid)
        .collection('userFollowing')
        .doc(u.uid)
        .get()
        .then((snapshot)=>{
            if(snapshot.exists){
            null;
            }
            else{
                if(firebase.auth().currentUser.uid!==u.uid)
                {
                    u.follow=false;
                    setUsers(arr=>[...arr,u]);
                }
            }
        })
    }
    return (
        
        <View style={{width:"100%",height:250,margin:10,borderTopEndRadius:15,borderTopStartRadius:15,borderBottomStartRadius:15,borderBottomEndRadius:15}}>
            <ScrollView   horizontal={true}  pagingEnabled={true} style={{width:screenWidth,}} >
            {users?
            (
               <FlatList
                    name="scrollone"
                    style={{width:screenWidth}}
                    horizontal={true}
                    data={users}
                    renderItem={({ item }) => 
                    (
                        
                        <View style={{flex:1,justifyContent:'space-around',backgroundColor:'white',flexDirection:'column',alignContent:'center',alignItems:'center',margin:10,borderTopEndRadius:15,borderTopStartRadius:15,borderBottomStartRadius:15,borderBottomEndRadius:15}}>
                           <View style={{alignContent:'center',alignItems:'center'}}>
                               {console.log(item)}
                                <TouchableOpacity onPress={()=>props.navigation('Explore',{uid:item.uid})} >
                                    {item.photo===undefined?
                                    <Image source={require('../../assets/undefined.png')} style={{width:90, height:90, borderRadius:45, margin:20}}/>
                                    :   <Image source={{uri:item.photo}} style={{width:90, height:90, borderRadius:45, margin:20}}/>
                                    }
                                </TouchableOpacity>
                                <Text>{item.username}</Text>
                            </View> 
                            {item.follow!==true?
                                <TouchableOpacity onPress={()=>onFollow(item.uid,item)} style={{height:30,width:100,backgroundColor:'#0095f6',color:'white',alignContent:'center',alignItems:'center',justifyContent:'center'}}>
                                    <Text style={{color:'white'}}>Follow</Text>
                                </TouchableOpacity>
                                :
                                <TouchableOpacity onPress={()=>onUnfollow(item.uid,item)} style={{height:30,width:100,backgroundColor:'#0095f6',color:'white',alignContent:'center',alignItems:'center',justifyContent:'center'}}>
                                    <Text style={{color:'white'}}>Following</Text>
                                </TouchableOpacity>
                            }      
                        </View>
                    )
                    
                }
                />
                
            ):
            (<View></View>)
            }
            </ScrollView>
        </View>
    )
}
