import React, { useState, useEffect } from 'react'
import { StyleSheet, View,Text, Image,TextInput, FlatList, Button,Dimensions,TouchableOpacity,ScrollView ,SafeAreaView} from 'react-native'
import moment from 'moment'

import db from '../../config/Firebase'
import * as firebase from 'firebase'
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height

export default function Search(props) {
    const [users, setUsers] = useState([]);
    const [user, setUser] = useState([]);
    const [posts,setPosts]=useState([]);
    useEffect(()=>{
          db.collection("post")
          .orderBy("creation", "desc")
            .onSnapshot((snapshot)=>{
               const post= snapshot.docs.map(doc=>{
                    return doc.data();
                })
                setPosts(post);
            })
    },[0])
  
    const fetchUsers = (search) => {
        firebase.firestore()
            .collection('users')
            .where('name', '>=', search)
            .get()
            .then((snapshot) => {
                let users = snapshot.docs.map(doc => {
                    const data = doc.data();
                    const id = doc.id;
                    const photo= doc.photo
                    return { id, ...data }
                });
                setUsers(users);
            })
    }

    const fetchUsersEnd=(item)=>{
      return   item.name="";
    }
    return (
    <View style={{height:screenHeight, flex:1, backgroundColor:'white',fontFamily:'logo-font',paddingTop:20,borderBottomColor:'lightgrey', borderBottomWidth:0.5,}}>
        {console.log(posts)}
        <View style={{
                    height:65, 
                    width:screenWidth,
                    flexDirection:"row",
                    position:'relative',
                    borderBottomWidth:0.5,
                    borderBottomColor:'lightgrey',
                    paddingBottom:5
                    }}>
                <View style={{paddingBottom:10,width:screenWidth,}}>
                    <View style={{flexDirection: "row",
              backgroundColor: "#FFF",
              borderColor:'grey',
              borderWidth:1.1,
              borderRadius: 40,
              alignItems: "center",
              paddingVertical: 10,
              paddingHorizontal: 20,
              }}>
                   <FontAwesome name="search" style={{ fontSize:23 }}   />
                    <TextInput
                    placeholder={ "  Search  "}
                    style={{width:screenWidth/0.7,height:30, fontSize: 15, color: "#ccccef" }}
                    onChangeText={(search) => fetchUsers(search)} onKeyPress={(search)=>fetchUsersEnd(search)} ></TextInput>
                    </View>
                </View>
        </View>

    {users.length!==0? 
        <View style={{width:screenWidth}}>
            <FlatList
                numColumns={1}
                horizontal={false}
                data={users}
                renderItem={({ item }) => (
                    <View style={{ height:50, backgroundColor:'white', flexDirection:'row', justifyContent:'space-between', alignItems:'center', borderBottomColor:'grey', borderBottomWidth:0.07}}>
                            <TouchableOpacity onPress={() => props.navigation.navigate("Profile", {uid: item.id})} style={{justifyContent:'center',alignItems:'center', flexDirection:'row'}}  >
                                {item.photo===undefined ?<Image source={require('../../assets/undefined.png')}  style={{width:32,height:32, borderRadius:32/2, margin:15,fontSize:23}} /> : <Image source={{uri:item.photo}} style={{width:32,height:32, borderRadius:32/2, margin:15}} />}
                                
                                 <Text style={{fontWeight:'bold', fontSize:15, }}>{item.username}</Text>
                            </TouchableOpacity>
                    </View>

                )}
            />
        </View>
        :
        <ScrollView style={{padding:10}}   horizontal={true}  pagingEnabled={true} style={{width:screenWidth,height:screenWidth}} >
                
            <FlatList
                style={{width:screenWidth,height:screenHeight}}
                    name="scrollone"
                    numColumns={3}
                    horizontal={false}
                    data={posts}
                    renderItem={({ item }) => (
                        <TouchableOpacity onPress={()=>props.navigation.navigate('UsersPost',{posts:item,uid:item.uid})} >
                                <Image source={{ uri: item.downloadURL} } style={{width:screenWidth/3, height:screenWidth/3,margin:3}}/>
                        </TouchableOpacity>
                    )}
             />
             
        </ScrollView>
          
    }         
    </View>
    )
}
