import React, { useState, useEffect } from 'react'
import { StyleSheet, Platform,View,Text,KeyboardAvoidingView,ImageBackground,AutoTags,Image,TextInput, FlatList, Button,Dimensions,TouchableOpacity,ScrollView ,SafeAreaView} from 'react-native'
import moment from 'moment'
import db from '../../config/Firebase'
import * as firebase from 'firebase'
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
import { Notifications } from 'expo';
require('firebase/firestore')
import uuid from "uuid";
import Chats from './Chats';
import { func } from 'prop-types'
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height
const keyboardVerticalOffset=Platform.OS==='ios'?120:100

export default function UserChat(props) {
    const [userPosts, setUserPosts] = useState([]);
    const [user, setUser] = useState([]);
    const [users, setUsers] = useState([]);
    const [message,setMessage]=useState([]);
    const [messages,setMessages]=useState([]);
    const [mess,setMess]=useState([]);
    const [user1,setUser1]=useState([]);
    const [m,setM]=useState([]);
    const [following,setFollowing]=useState(null);
    useEffect(()=>{
        
              db.collection("users")
                .doc(firebase.auth().currentUser.uid)
                .get()
                .then((snapshot) => {
                    if (snapshot.exists) {
                        let user = snapshot.data();
                        user.uid = snapshot.id;
                        setUser(user);
                    }
                    else {
                        console.log('does not exist')
                    }
                })
              db.collection("users")
                .doc(props.route.params.uid)
                .get()
                .then((snapshot) => {
                    if (snapshot.exists) {
                        let user = snapshot.data();
                        user.uid = snapshot.id;
                        setUsers(user)
                    }
                    else {
                        console.log('does not exist')
                    }
                })              
      db.collection("message")
        .orderBy("sendAt", "asc")
        .get()
        .then((snapshot)=>{
            const mes2=snapshot.docs.map(doc=>{
                let message=doc.data();
                message.id=doc.id;
                fetchMessage(message);
                return message;
            })            
            setMessages(mes2);          
        })

        return(()=>{
            setMessages('');
            console.log('clear message');
        })
        
    },[props.route.params.uid])
    function fetchMessage(m){
        db.collection("users")
                .doc(m.sendBy)
                .get()
                .then((snapshot) => {
                    if (snapshot.exists) {
                        let user = snapshot.data();
                        user.uid = snapshot.id;
                        m.sender=user;
                    }
                    else {
                        console.log('does not exist')
                    }
                })
                db.collection("users")
                .doc(m.sendTo)
                .get()
                .then((snapshot) => {
                    if (snapshot.exists) {
                        let user = snapshot.data();
                        user.uid = snapshot.id;
                        m.receiver=user;
                    }
                    else {
                        console.log('does not exist')
                    }
                })
    }
    function onSend(mesg,uid,id){
        try{
        if(mesg!=""){
            const message={message:mesg,sendAt:new Date().getTime(),sendBy:id,sendTo:uid,seento:false,seenby:true}
            const send= firebase.firestore().collection('message').add(message)
                      
                      
            if(send)
                {
                    setMessages(arr=>[...arr,message])
                }
            else{
                alert("Message not send")
            }
            
        }
       setMessage('');
        }catch(e){
          alert(e)  
        }
    }
    function redirect(){
        setMessages('');
        props.navigation.navigate('Chat');
    }
    return (
        <View  style={{flex:1, width:screenWidth,height:screenHeight, backgroundColor:'white',fontFamily:'logo-font',marginTop:20}}>
             {console.log(props.route.params)}
            <View style={{height:60, width:screenWidth, borderBottomColor:'rgba(0,0,0,0.1)', borderBottomWidth:0.7,  justifyContent:'flex-start', flexDirection:"row",position:'relative'}}>
                    <FontAwesome onPress={()=>redirect()} name="arrow-left" style={{fontSize:23, marginLeft:12,marginTop:18,marginBottom:18}} />                
                <View style={{flexDirection:'row',alignContent:'center'}}>

                    <TouchableOpacity style={{justifyContent:'center',alignItems:'center', flexDirection:'row'}}  onPress={()=> props.navigation.navigate('Profile', {uid:users.uid })}>
                        {
                            users.photo===undefined?
                            <Image source={require('../../assets/undefined.png')}  style={{width:40,height:40, borderRadius:40/2,marginLeft:15,margin:12}} /> 
                            :
                            <Image source={{uri:users.photo}} style={{width:40,height:40, borderRadius:40/2,marginLeft:15,margin:12}} />
                            
                        }
                        
                        <Text style={{fontWeight:'bold', fontSize:15, }}>{users.username}</Text>

                    </TouchableOpacity>
                </View>
                
            </View>
            
        <KeyboardAvoidingView behavior={Platform.OS==='ios'?"padding":null} keyboardVerticalOffset={keyboardVerticalOffset} style={{flex:1, justifyContent:'center', alignItems:'center'}}>
            
            <ScrollView>
                <FlatList
                    style={{flex:1}}
                    name="scrollone"
                    data={messages}
                    renderItem={({ item }) => (
                        <Chats  messages={item} user={user} users={users} />

                        )}
                />
            </ScrollView>
            <View style={{alignContent:'center',backgroundColor:'white',borderTopWidth:0.5,borderTopColor:'grey',alignItems:'center',flexDirection:"row",width:screenWidth}}>
                            <TextInput
                            style={{height: 50, width:'85%',borderRadius:50/2,  color:'black', paddingHorizontal:20,paddingVertical:10, margin:3, borderRadius:10, borderColor:'grey',color:'black'}}
                             placeholderTextColor={'grey'}
                             value={message}
                             placeholder={'Send Message'}
                             onChangeText={(message)=>setMessage(message)}
                            />
                            <TouchableOpacity  onPress={()=>onSend(message,users.uid,firebase.auth().currentUser.uid)}>
                                    <Text
                                        style={[
                                            (!message)?
                                            {fontWeight:'bold',color:'beige'}
                                            :{color:'grey'}
                                        ]}
                                    >SEND</Text>
                            </TouchableOpacity>
            </View>
        </KeyboardAvoidingView> 

        </View>

    )
}
