import React, { useState, useEffect, version } from 'react'
import { StyleSheet, View,Text, Image,TextInput, FlatList, Button,Dimensions,TouchableOpacity,ScrollView ,SafeAreaView} from 'react-native'
import moment from 'moment'
import db from '../../config/Firebase'
import * as firebase from 'firebase'
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height

function UsersPost(props) {
    const [user,setUser]=useState([]);
    const [posts,setPosts]=useState([]);
    const [post,setPost]=useState({user:user,posts:posts,like:likes});
    const [users,setUsers]=useState([]);
    const [likes,setLikes]=useState(null);
    const [like,setLike]=useState([]);
    const [following,setFollowing]=useState(null);
    const [save,setSave]=useState(null);
    useEffect(()=>{
        db.collection("following")
        .doc(firebase.auth().currentUser.uid)
        .collection("userFollowing")
        .doc(props.route.params.uid)
        .get()
        .then((snapshot)=>{
                if(snapshot.exists)
                    setFollowing(true);
                else
                    setFollowing(false);
        })
        
        db.collection('savepost')
    .doc(firebase.auth().currentUser.uid)
    .collection('usersave')
    .doc(props.route.params.posts.id)
    .get()
    .then((snapshot)=>{
        if(snapshot.exists)
             setSave(true);
          else
              setSave(false);
    })

        firebase.firestore()
        .collection("posts")
        .doc(props.route.params.uid)
        .collection("userPosts")
        .doc(props.route.params.posts.id)
        .collection("likes")
        .onSnapshot((snapshot)=>{
            let likesss=snapshot.docs.map(doc=>{return doc.data();})
            setLike(likesss)
        })
        firebase.firestore()
            .collection("posts")
            .doc(props.route.params.uid)
            .collection("userPosts")
            .doc(props.route.params.posts.id)
            .collection("likes")
            .doc(firebase.auth().currentUser.uid)
            .onSnapshot((snapshot) => {
                setLikes(false)
                if(snapshot.exists){
                    setLikes(true);  
                }

                    
                return(likes);
            })
        db.collection('users')
        .doc(firebase.auth().currentUser.uid)
        .get()
        .then((snapshot)=>{
           if(snapshot.exists){
            let user = snapshot.data();
            user.uid = snapshot.id;
            setUsers(user)
           }
           else{
              alert('User does not exists')
           }
            
        })
        
        db.collection('users')
        .doc(props.route.params.uid)
        .get()
        .then((snapshot)=>{
           if(snapshot.exists){
            let user = snapshot.data();
            user.uid = snapshot.id;
            setUser(user)
           }
           else{
              alert('User does not exists')
           }
            
        })
        db.collection("posts")
        .doc(props.route.params.uid)
        .collection("userPosts")
        .doc(props.route.params.posts.id)
        .get()
        .then((snapshot) => {
            let post=snapshot.data()
                post.id=snapshot.id;
            setPosts(post)
        })

    },[])
    const onFollow = (userToFollow) => {
        db.collection("following")
             .doc(firebase.auth().currentUser.uid)
             .collection("userFollowing")
             .doc(props.route.params.uid)
             .set({})
         db.collection('users').doc(firebase.auth().currentUser.uid).update({
                 following: firebase.firestore.FieldValue.arrayUnion(userToFollow)
             })
         db.collection('users').doc(props.route.params.uid).update({
             followers: firebase.firestore.FieldValue.arrayUnion(firebase.auth().currentUser.uid)
             })
             setFollowing(true)
     }
     const onUnfollow = (userToFollow) => {
        db.collection("following")
             .doc(firebase.auth().currentUser.uid)
             .collection("userFollowing")
             .doc(props.route.params.uid)
             .delete()
             db.collection('users').doc(firebase.auth().currentUser.uid).update({
                 following: firebase.firestore.FieldValue.arrayRemove(userToFollow)
             })
             db.collection('users').doc(props.route.params.uid).update({
                 followers: firebase.firestore.FieldValue.arrayRemove(firebase.auth().currentUser.uid)
             })
             setFollowing(false)
     }
    const onDeleteSavePost=(post,user)=>{
        let r=firebase.firestore()
            .collection('savepost')
            .doc(firebase.auth().currentUser.uid)
            .collection("usersave")
            .doc(post)
            .delete()
            if(r)
                setSave(false);
            else
                setSave(true);
    }
    const onSavePost=(post,user)=>{

        const message={id:post,user:user,creation:new Date().getTime(),savedBy:firebase.auth().currentUser.uid}
        let f=firebase.firestore()
            .collection('savepost')
            .doc(firebase.auth().currentUser.uid)
            .collection("usersave")
            .doc(post)
            .set(message)
            if(f){
                setSave(true);
            }
            else{
                setSave(false);
            }
    }
    const onPostDelete=(userid,postid)=>{
        try{
            
           
            db.collection('post').doc(postid).delete().then(function(){
                db.collection('posts').doc(userid).collection('userPosts').doc(postid).delete()

            })

            alert('Post will Be Deleted')
            props.navigation.navigate('Profile')
            
        }catch(e){
            console.log(e)
        }

    }
    const onLikePress = (userId, postId) => {
        firebase.firestore()
            .collection("posts")
            .doc(userId)
            .collection("userPosts")
            .doc(postId)
            .collection("likes")
            .doc(firebase.auth().currentUser.uid)
            .set({})
            const uid=firebase.auth().currentUser.uid

            const len=firebase.firestore()
            .collection("posts")
            .doc(userId)
            .collection("userPosts")
            .doc(postId)
            .update({
                likesCount: firebase.firestore.FieldValue.arrayUnion(uid)
            })
    }
    const onDislikePress = (userId, postId) => {
        firebase.firestore()
            .collection("posts")
            .doc(userId)
            .collection("userPosts")
            .doc(postId)
            .collection("likes")
            .doc(firebase.auth().currentUser.uid)
            .delete()
    
            firebase.firestore()
            .collection("posts")
            .doc(userId)
            .collection("userPosts")
            .doc(postId)
            .update({
				likesCount: firebase.firestore.FieldValue.arrayRemove(firebase.auth().currentUser.uid)
			})
    }
  return(
    <View style={{flex:1, width:screenWidth, backgroundColor:'white', }}>
        {console.log(following)}
                    <View style={{width:screenWidth, height:50, backgroundColor:'white', flexDirection:'row', justifyContent:'space-between', alignItems:'center', borderBottomColor:'grey', borderBottomWidth:0.07}}>
                        <View style={{justifyContent:'center',alignItems:'center', flexDirection:'row'}}>
                            <TouchableOpacity onPress={()=> props.navigation.navigate('Profile', {uid:user.uid })} style={{justifyContent:'center',alignItems:'center', flexDirection:'row'}} >
                                {user.photo===undefined ?
                                    <Image source={require('../../assets/undefined.png')}  style={{width:40,height:40, borderRadius:40/2, margin:10}} /> 
                                :   <Image source={{uri:user.photo}} style={{width:40,height:40, borderRadius:40/2, margin:10}} />
                                }
                                <Text style={{fontWeight:'bold', fontSize:15, }}>{user.username} </Text>
                                <Text style={{fontSize:40,marginBottom:22,fontWeight:'bold'   }}>. </Text>
                            </TouchableOpacity>
                            {props.route.params.uid==firebase.auth().currentUser.uid?
                                null
                        :following===true?
                            <TouchableOpacity onPress={()=>onUnfollow(props.route.params.uid)}>
                                <Text style={{fontWeight:'bold', fontSize:15, }}>following</Text>
                            </TouchableOpacity>
                            :
                            <TouchableOpacity onPress={()=>onFollow(props.route.params.uid)}>
                                <Text style={{color:'#0095f6',fontWeight:'bold', fontSize:15,}}>follow</Text>
                            </TouchableOpacity>
                        }
                        </View>
                        {/* <Text style={{margin:15}}>{moment(this.props.item.date).format('ll')}</Text> */}
                        

                        {user.uid===firebase.auth().currentUser.uid 
                        ?
                        
                            <TouchableOpacity  onPress={()=>onPostDelete(user.uid, posts.id)} style={{alignItems:'center'}}>
                                <FontAwesome name="times" style={{fontSize:27,width:25,height:25, margin:10,shadowOpacity:20,marginTop:20,marginBottom:20}} />
                            </TouchableOpacity>
                        :
                            <TouchableOpacity>
                                <Image source={require('../../assets/3-dot.png')} style={{width:25,height:25, margin:10,shadowRadius:15,borderRadius:8,shadowOpacity:20,marginTop:20,marginBottom:20}} />
                            </TouchableOpacity>
                        }

                    </View>
                    <View style={{borderRadius:0.2,borderColor:'grey'}}>
                        
                            {posts.width===undefined && posts.height===undefined?
                                 likes===true?
                                 (
                                 <TouchableOpacity onPress={() => onDislikePress(props.route.params.uid, posts.id)}>
                                        <Image style={{width:screenWidth, height:360, }} source={{ uri: posts.downloadURL }} />
                                </TouchableOpacity>)
                                    :(
                                    <TouchableOpacity onPress={() => onLikePress(props.route.params.uid, posts.id)}>
                                        <Image style={{width:screenWidth, height:360, }} source={{ uri: posts.downloadURL }} />
                                    </TouchableOpacity>
                                    ) 
                                :  likes===true?
                                (
                                <TouchableOpacity onPress={() => onDislikePress(props.route.params.uid, posts.id)}>
                                       <Image style={{width:"100%", height:360, }} source={{ uri: posts.downloadURL }} />
                               </TouchableOpacity>)
                                   :(
                                   <TouchableOpacity onPress={() => onLikePress(props.route.params.uid, posts.id)}>
                                       <Image style={{width:"100%", height:360, }} source={{ uri: posts.downloadURL }} />
                                   </TouchableOpacity>
                                   )  
                            }                    
                    </View>
                    <View style={{width:screenWidth, flexDirection:"row", justifyContent:"space-between", height:50, alignItems:'center'}}>
                         <View style={{justifyContent:'center', flexDirection:'row', alignItems:'center'}}>
                            { likes===true ?
                                (
                                <TouchableOpacity onPress={() => onDislikePress(props.route.params.uid, posts.id)} >
                                    <FontAwesome   style={{fontSize:27,width:30,height:30, margin:10}} name="heart" color='red' />
                                </TouchableOpacity>  
                                )
                                :
                                (
                                <TouchableOpacity  onPress={() => onLikePress(props.route.params.uid, posts.id)} >
                                    <FontAwesome  style={{fontSize:27,width:30,height:30, margin:10}} name='heart-o' color='red' />
                                </TouchableOpacity>
                                )
                            }
                            <TouchableOpacity onPress={() => props.navigation.navigate('Comment', { postId: posts.id, uid: user.uid })}>            
                                    <FontAwesome name="comment-o" style={{width:30,fontSize:27,height:30, margin:10}} />
                            </TouchableOpacity>    
                             <FontAwesome name='send-o' style={{width:30,height:30, margin:10,fontSize:27}} />    
                         </View>
                            {save===false?
                                <TouchableOpacity onPress={()=>onSavePost(posts.id,props.route.params.uid)}>
                                      <FontAwesome name="bookmark-o" style={{width:30,height:30, margin:10,fontSize:27}} />
                                </TouchableOpacity>
                                :     
                                <TouchableOpacity onPress={()=>onDeleteSavePost(posts.id,props.route.params.uid)}>
                                      <FontAwesome name="bookmark" style={{width:30,height:30, margin:10,fontSize:27}} />
                                </TouchableOpacity>                        
                            }
                    </View>
                    <View>
                        <TouchableOpacity onPress={() => props.navigation.navigate('Likes', { postId: posts.id, uid: user.uid,posts:posts })}>
                                <Text style={{fontWeight:'bold', marginHorizontal:10, marginTop:0 }}>{like.length}{console.log(props.route.params)} likes</Text>
                        </TouchableOpacity>
                                <View style={{flexDirection:"row",  marginTop:5}}>
                                    <Text style={{fontWeight:'bold', marginLeft:10}}>{user.username} </Text>
                                    <Text >{posts.caption}</Text>

                                </View>
                    </View>  
                    <TouchableOpacity onPress={() => props.navigation.navigate('Comment', { postId: posts.id, uid: user.uid })}>
                            <Text style={{marginHorizontal:10, color:"grey",  marginTop:5}}>Show all the comments: </Text>
                    </TouchableOpacity>
                        <View style={{justifyContent:'space-between',alignItems:'center', flexDirection:'row'}}>
                            <View style={{flexDirection:'row', alignItems:'center'}}>
                             {users.photo===undefined  ?<Image source={require('../../assets/undefined.png')} style={{width:28,height:28, borderRadius:28/2, marginHorizontal:10, marginTop:5,marginBottom:5}}/>
                                    : <Image source={{uri:users.photo}} style={{width:28,height:28, borderRadius:28/2, marginHorizontal:10, marginTop:5,marginBottom:5}}/>}
                                <TextInput onKeyPress={(data)=>console.log(data)} placeholder="Add a comment..." style={{ color:"grey",  marginTop:5}}></TextInput>
                            </View>
                            
                        </View>  
                    
    </View>
  )
}
export default UsersPost;