import React, { useState, useEffect } from 'react'
import { StyleSheet, View,TextInput, Text, Image, FlatList, Button,SafeAreaView,Dimensions,TouchableOpacity,ScrollView, ScrollViewBase } from 'react-native'
import *as firebase from 'firebase'
require('firebase/firestore')
import { connect } from 'react-redux'
import { FontAwesome } from '@expo/vector-icons';
import moment from 'moment'
import db from './../../config/Firebase';
import { useFonts } from 'expo-font';
import Chat from './Chat';
import UsersPosts from './UsersPosts';
const screenWidth = Dimensions.get("window").width
const screenHeight = Dimensions.get("window").height

export default function UserPhoto(props) {
    const [user,setUser]=useState([]);
    useEffect(()=>{
        db.collection('users')
        .doc(firebase.auth().currentUser.uid)
        .get()
        .then((snapshot)=>{
            if(snapshot.exists)
            {
                let users=snapshot.data();
                users.uid=snapshot.id;
                setUser(users);
            }
        })
    },[0])
    return (
        <View style={{ justifyContent:'center',alignItems:'center'}}>
            {user.photo===undefined
                ? 
                    <Image  source={require('../../assets/undefined.png')}  style={{width:30,height:30, borderRadius:30/2,borderColor:'white',borderWidth:2}} /> 
                :   
                    <Image source={{uri:user.photo}}  style={{width:30,height:30, borderRadius:30/2,}} /> 
            }
        </View>
    )
}
